import request from 'supertest';
import { expect } from 'chai';
import { app } from '../app';

describe('GET /', function(){
	it('should have a status code 0f 200', function(done){
		request(app).get('/')
								.expect(200)
								.end(function(err, res){
									if(err) done(err);
									done();
								});
	});
})